import React from 'react';


const img = "https://img.icons8.com/ios/50/undefined/ok--v1.png";

const modalbox = {
    backgroundColor: 'white',
    padding: '1rem',
    maxWidth: '700px',
    width: '100%',
    height: 'auto',
    margin: '0 auto',
    borderRadius: '5px',
    minHeight: '200px',
    filter: 'drop-shadow(0px 7px 14px rgba(0, 0, 0, 0.67))'
}

const textBox  = {
}


const titleEelm  = {
    fontWeight: 'bold'
}

const displayBlock  ={
        
         width: '100%',
        height: '100%',
        overflow: 'hidden',
        position: 'absolute',
        zIndex: '9',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        visibility: 'visible',
        top:'0',
        fontSize: '1.25rem',
        fontFamily: 'Arial, Helvetica, sans-serif',
        color: 'green',
}

const imgD = {
    float: 'left',
    paddingRight: '2rem'
}

const closeOne  ={
    width: '28px',
    height: '28px',
    backgroundColor: 'rgb(255, 255, 255)',
    border: '3px solid #000',
    borderRadius: '3px',
    fontWeight: 'bold',
    padding: '5px',
    fontSize: '12px',
    cursor:'pointer',
    fontFamily: 'Arial, Helvetica, sans-serif',
    position: 'absolute',
    right: '5px',
    top: '5px',
}

const title = 'Title';
const text = 'text';



export function Modal(title, text, setShow) {
    return (
        <>
         <div style={displayBlock}>
              <div style={modalbox}>
              <div style={textBox}>
              <img style={imgD} src={img} alt="success"/>
              <p style={titleEelm}>{title}</p>
              {text}
              </div>
              <button style={closeOne}  onClick={() => setShow(false)}>
                  X
              </button>
            </div>
        </div>
         </>
    );
};